@import './styles/mixins.scss';

* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

html,
body {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  background: white;
}

#root {
  position: relative;
  width: 100%;
}
